
import vrv from 'common/js/vrv-jssdk'
import Block from 'base/block/block'
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
import VHeader from '@/components/v-header/v-header.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import { ERR_OK } from 'api/config'
import { saveCollectArticles, judgeCollectArticles } from 'api'
import { mapGetters, mapMutations, mapActions } from 'vuex'

export default {
name: 'detail',
mixins: [],
props: {
detail: {
type: Object,
default () {
return {}
}
}
},
data () {
return {
showMod: false,
menus: {},
isFavorite: false
}
},
computed: {
modMenus () {
return this.isFavorite ? {

wumaohoutai: '已收藏',
copyUrl: '复制链接'
} : {

collectButton: '收藏',
copyUrl: '复制链接'
}
},
...mapGetters([
'detailShowFlag'
])
},
components: {
Block,
Scroll,
Loading,
VHeader,
Actionsheet
},
watch: {
},
methods: {
hide () {
this.closeDetail()
},
showMenuMod () {
this.showMod = true
this._judgeCollectArticles(this.detail.id)
},
clickMenu (menu) { // 收藏或转发

if (menu === 'collectButton') this.collect()
if (menu === 'copyUrl') this.copy(this.detail.url)
},
collect () {
this._saveCollectArticles()
},
goUrl (url) {
vrv.jssdk.showNavigationBar({ show: true })
setTimeout(window.location.href = url, 500)
},
copy (url) {
vrv.jssdk.copyTextToPaste({ url })
},
_saveCollectArticles () {
const params = {
url: this.detail.url,
title: this.detail.title,
articleId: this.detail.id,
webSite: this.detail.webSite
}
saveCollectArticles(params).then(data => {
if (data.code === ERR_OK) {
this.$vux.toast.show({
text: '收藏成功',
time: 500,
type: 'success'
})
}
})
},
_judgeCollectArticles (id) {
const params = {
articleId: id
}
judgeCollectArticles(params).then(data => {
if (data.code === ERR_OK) {
if (data.obj) this.isFavorite = true
else this.isFavorite = false
}
})
},
vrvReady () {
vrv.ready(() => {
try {
vrv.jssdk.showNavigationBar({ show: false })
} catch (err) {
console.log(err)
}
})
},
...mapMutations({
'toggleDetailPage': 'TOGGLE_DETAIL_PAGE'
}),
...mapActions([
'closeDetail'
])
},
created () {
this.vrvReady()
},
mounted () {
}
}



