export default {
  name: 'tab',
  mixins: [],
  data () {
    return {
      show: true
    }
  },
  computed: {},
  components: {
  },
  watch: {},
  methods: {
  },
  created () {},
  mounted () {
    var winHeight = window.innerHeight
    window.addEventListener('resize', (e) => {
      var tempHeight = window.innerHeight
      if (tempHeight < winHeight) {
        this.show = false
      } else this.show = true
    })
  },
  beforeDestory () {
    window.removeEventListener('resize', this, false)
  }
}



